ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  margin: 0;
  padding: 0;
  color: $primary-title-color;
}
h1,
h2,
h3,
h4 {
  margin: 0;
}
p {
  margin: 0;
}
hr {
  margin: 0px;
}
input {
  outline: none;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
  padding: 0;
  margin: 0;
}
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&family=Raleway:wght@700&display=swap');
body {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  font-family: $primary-font-family;
  font-weight: 400;
  color: $primary-text-color;
}
.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 30px;
  padding-right: 30px;
  @media screen and (min-width: 480px) {
    width: 480px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
    padding-left: 40px;
    padding-right: 39px;
  }
  @media screen and (min-width: 1200px) {
    width: 1200px;
    padding-left: 40px;
    padding-right: 40px;
  }
}
.section {
  padding: 84px 0;
  @media screen and (min-width: 768px) {
    padding: 100px 0;
  }
  @media screen and (min-width: 1280px) {
    padding: 130px 0;
  }
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.hidden {
  overflow: hidden;
}
